.news-card
{
    display: flex;
    flex-direction: column;
    border-radius: 12px;
    background: white;
    box-shadow: 10px 10px 5px 0px #888888;
    padding: 0.1rem;
    width: 18rem;
    margin-bottom: 1rem;
    height: auto;
    gap: 0.5rem;
    font-family: var(--font-family);

}

.news-card-image
{
    
    height: 12rem;
    margin-bottom: 0.5rem;
}

.news-card-image img
{
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
    object-fit: contain;
    
}

.news-card-body{
    padding: 0rem 1rem 1rem 1rem;
}

.news-card-title
{
    font-size: 1.3rem;
    font-weight: 600;
    margin-bottom: 0.5rem;
    height: 5rem;
}
.news-card-text
{
    height: 6rem;
}

.read-more
{
    height: 5rem;
    font-size: 0.9rem;
}
.news-card-text
{
    font-size: 1rem;
}
.text-muted
{
    font-size: 0.85rem;
    align-self: flex-end;
}
.news-card-text a
{
    color: blue;
    margin-left: 0.1rem;
}

.news-card-text a:hover
{
    color: green;
}


